<template>
  <div class="answerManage">
    <div class="left">
      <ul>
        <li v-for="(item, index) in questionList" :key="item.questionInfo.id"
            @click="selectQuestion(item.questionInfo, index)" :class="[selectIndex === index ? 'select' : '']">
          <div class="top">
            <van-button style="position: absolute;right: 1rem;top: 2.3rem;" type="danger" size="mini"
                        @click="delQuestion(item.questionInfo.id, index)">删除
            </van-button>
            <div class="left">
              <Avatar icon="ios-person" size="small" :src="item.userInfo.avatar"/>
            </div>
            <div class="right">
              <div class="title ellipsis">
                <svg t="1579084662468" class="icon" viewBox="0 0 1024 1024" version="1.1"
                     xmlns="http://www.w3.org/2000/svg" p-id="8640" width="200" height="200">
                  <path d="M512.6 512.5m-447.4 0a447.4 447.4 0 1 0 894.8 0 447.4 447.4 0 1 0-894.8 0Z" fill="#27ADCC"
                        p-id="8641"></path>
                  <path
                      d="M512.2 807.1c-75.2 0-137.7 54.6-149.9 126.3 46.9 16.7 97.3 25.7 149.9 25.7s103-9.1 149.9-25.7c-12.3-71.7-74.7-126.3-149.9-126.3zM554.9 749.8h-87.5v-43.7c0-25.4 5.1-48.1 15.4-67.9 10.2-19.9 29.8-43.1 58.6-69.8 28.9-26.7 48.4-47.8 58.6-63.3 10.2-15.5 15.4-33.2 15.4-53.1 0-29.2-9.3-52.3-27.9-69.3-18.6-17.1-43.7-25.6-75.4-25.6-54 0-90.6 29.8-109.8 89.3l-80-33.5c14.9-41 39.1-74.1 72.6-99.6 33.5-25.4 72.6-38.2 117.3-38.2 55.8 0 101.6 16.6 137.3 49.8 35.7 33.2 53.5 75.5 53.5 127 0 26.1-6.5 51-19.5 74.9-13 23.9-37.5 52.3-73.5 85.2-36 32.9-54.3 65.5-54.9 97.7v40.1z"
                      fill="#FFFFFF" p-id="8642"></path>
                </svg>
                {{ item.questionInfo.title }}
              </div>
              <div class="info">
                <span>{{ item.userInfo.username || '未知' }}</span><span>发起了提问</span><span>·</span><span>{{ item.questionInfo.createdAt }}</span>
              </div>
            </div>
          </div>
        </li>
      </ul>
      <van-divider dashed>{{ questionList.length === 0 ? '暂无相关答疑' : '已经到底了' }}</van-divider>
    </div>
    <div class="right">
      <van-divider v-if="!select" dashed>请先选择一个问题进行回答</van-divider>
      <section v-else class="answer-list">
        <div class="top">
          <ul class="ul">
            <li>
              <div class="left">问题标题：</div>
              <div class="right">{{ selectQuestionInfo.title }}</div>
            </li>
            <li>
              <div class="left">问题内容：</div>
              <div class="right">{{ selectQuestionInfo.content }}</div>
            </li>
            <li>
              <div class="left">回答：</div>
              <div class="right">
                <Input maxlength="200" show-word-limit v-model="content" type="textarea"
                       :autosize="{minRows: 5,maxRows: 8}" placeholder="请输入回答内容"
                />
              </div>
            </li>
            <li>
              <div class="left"></div>
              <div class="right">
                <Button @click="addAnswer" type="primary">回答</Button>
              </div>
            </li>
          </ul>
        </div>
        <div class="answer-item">
          <ul>
            <li v-for="(answer, index) in answerList" :key="answer.answerInfo.id">
              <div class="content">
                <span><van-tag v-if="answer.role === 1" type="success">管理员（{{ answer.adminInfo.username }}）</van-tag><van-tag
                    v-else
                    type="primary">用户（{{ answer.userInfo.username || '未知' }}）</van-tag> 于</span><span>·</span><span>{{ answer.answerInfo.createdAt }}</span><span>·</span><span>回答了此问题</span>
                <p style="padding: 1rem;">{{ answer.answerInfo.content }}</p>
              </div>
              <van-button v-if="answer.role === 1 && adminInfo.id === answer.adminInfo.id" style="position: absolute;right: 1rem;top:1rem;" type="danger"
                          size="mini" @click="delAnswer(answer.answerInfo.id, index)">删除
              </van-button>
            </li>
          </ul>
        </div>
        <van-divider dashed>{{ answerList.length === 0 ? '还没有解答内容哦' : '已经到底了' }}</van-divider>
      </section>
    </div>
  </div>
</template>

<script>
import {addAnswer, delAnswer, delQuestion, getAnswerList, getQuestionList} from "../../../api";
import config from "../../../config";
import moment from "moment";
import {mapState} from 'vuex'

export default {
  name: "answerManage",
  data() {
    return {
      questionList: [],
      answerList: [],
      select: false,
      content: '',
      adminId: config.getCache('adminId'),
      selectQuestionInfo: {},
      role: 1,
      selectIndex: -1
    }
  },
  mounted() {
    this.getQuestionList();
  },
  computed: {
    ...mapState(['adminInfo'])
  },
  methods: {
    selectQuestion(selectQuestionInfo, selectIndex) {
      this.selectQuestionInfo = selectQuestionInfo;
      this.select = true;
      this.content = '';
      this.selectIndex = selectIndex;
      this.getAnswerList();
    },
    async getAnswerList() {
      let {id: questionId} = this.selectQuestionInfo;
      let data = await getAnswerList({
        questionId
      });
      if (data.code === 200) {
        let answerList = [];
        data.result.answerList.forEach(item => {
          let {adminInfo, userInfo, role, answerInfo} = item;
          answerInfo.createdAt = moment(answerInfo.createdAt).format('MM-DD');
          answerList.push({
            userInfo,
            adminInfo,
            role,
            answerInfo
          })
        });
        this.answerList = answerList;
        // eslint-disable-next-line no-console
        console.log(answerList);
      } else {
        this.$Message.warning(data.data);
      }
    },
    async addAnswer() {
      let {content, adminId, role} = this;
      let {id: questionId} = this.selectQuestionInfo;
      if (!content) {
        return this.$Message.warning('请填写回答内容');
      }
      if (!adminId) {
        return this.$Message.warning('请先登录');
      }
      if (!questionId) {
        return this.$Message.warning('请先选择问题');
      }
      let data = await addAnswer({
        content, adminId, role, questionId
      });
      if (data.code === 200) {
        this.getAnswerList();
        this.content = '';
        this.$Message.success(data.data);
      } else {
        this.$Message.error(data.data);
      }
    },
    delAnswer(id, index) {
      this.$Modal.confirm({
        title: '删除回答',
        content: '确认删除此回答吗？',
        okText: '删除',
        onOk: async () => {
          let data = await delAnswer({
            id
          });
          if (data.code === 200) {
            this.answerList.splice(index, 1);
            this.$Message.success(data.data);
          } else {
            this.$Message.error(data.data);
          }
        }
      });
    },
    delQuestion(id, index) {
      this.$Modal.confirm({
        title: '删除提问',
        content: '确认删除此提问吗？',
        okText: '删除',
        onOk: async () => {
          let data = await delQuestion({
            id
          });
          if (data.code === 200) {
            this.select = false;
            this.selectQuestionInfo = {};
            this.content = '';
            this.questionList.splice(index, 1);
            this.$Message.success(data.data);
          } else {
            this.$Message.error(data.data);
          }
        }
      });
    },
    async getQuestionList() {
      let adminId = config.getCache('adminId');
      let data = await getQuestionList({
        adminId
      });
      if (data.code === 200) {
        let questionList = [];
        data.result.questionList.forEach(item => {
          let {userInfo, questionInfo} = item;
          questionInfo.createdAt = moment(questionInfo.createdAt).format('MM-DD');
          questionList.push({
            userInfo,
            questionInfo
          })
        });
        this.questionList = questionList;
        // eslint-disable-next-line no-console
        console.log(questionList);
      } else {
        this.$Message.warning(data.data);
      }
    }
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
.answerManage {
  width: 100%
  height: 100%
  overflow: hidden;
  display flex

  > .left {
    flex 4
    max-width 45rem
    min-height 45rem
    height: 100%
    background-color: #fff
    overflow auto
    padding: 1rem

    > ul {
      width: 100%

      > li {
        width: 100%
        padding: 1rem
        border-bottom: .1rem solid #f1f1f1
        transition all .3s
        cursor pointer

        &.select {
          background-color: #f1f1f1
        }

        &:hover {
          background-color: #f9f9f9
        }

        > .top {
          position relative
          width: 100%
          height: 5rem
          overflow: hidden;
          display flex

          > .left {
            max-width 3rem
            min-width 3rem
            flex 2
            display flex
            align-items: center;
            justify-content center
          }

          > .right {
            flex 8
            max-width 30rem
            padding: 0 0 0 .5rem

            > .title {
              color #616161
              font-size: 1.5rem
              height: 2.5rem

              > svg {
                position relative
                top: .2rem
                margin: 0 .5rem
                width: 1.5rem
                height: 1.5rem
              }
            }

            > .info {
              padding-left: .5rem

              > span {
                margin-right: 1rem
                color #919191
                font-size: 1.2rem
              }
            }
          }
        }
      }
    }
  }

  > .right {
    flex 6
    height: 100%
    overflow auto
    background-color: #f9f9f9
    padding: 1rem

    > .answer-list {
      width: 100%

      > .top {
        margin: .5rem 1rem 1rem 1rem
        background-color: #fff
      }

      > .answer-item {
        width: 100%
        position relative

        > ul {
          width: 100%
          padding: 1rem

          > li {
            position relative
            padding: 1rem
            background-color: #fff
            border-bottom .1rem solid #f9f9f9
            font-size: 1.4rem
            color #5d5d5d

            > .content {
              > span {
                margin-right: 1rem
                color #919191
                font-size: 1.2rem
              }
            }
          }
        }
      }
    }
  }
}
</style>
